import { Button, Dialog, DialogTitle, DialogContent, TextField, DialogActions } from "@mui/material";
import  { useState } from "react";
import { AddMark, Film, fireauth, Mark, Now } from "../../utils/mybase";
import { State } from "../../utils/types";

const Write = (prop: { v: State<boolean>, film: Film }) => {

  const Submit = () => {
    var a = {
      comment:description,
      vote:Number(vote),
      uid:fireauth.currentUser?.uid,
      date:Now()
    } as Mark 

    AddMark(a,prop.film.id)
    handleClose()

  }

  const handleClose = () => {
    prop.v[1](false);
  };

  const [description, setDescription] = useState("")
  const [vote, setVote] = useState("0")


  return (

    <Dialog open={prop.v[0]} onClose={handleClose} onSubmit={Submit} >
      <DialogTitle>Add Rate</DialogTitle>
      <DialogContent sx={{ padding: "10px" }}>
        <DialogContent>
          this vote will be sent from {fireauth?.currentUser?.email}
        </DialogContent>

        <br />

        <DialogContent>
          <TextField
            id="filled-number"
            label="Vote"
            type="number"
            value={vote}
            onChange={e => setVote(e.target.value)}

          />
        </DialogContent>



        <DialogContent>
          <TextField
            fullWidth
            value={description}
            onChange={e => setDescription(e.target.value)}
            label="Description"
            multiline
            rows={4}

          />
        </DialogContent>





      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose}>Cancel</Button>
        <Button onClick={Submit}>Submit</Button>
      </DialogActions>
    </Dialog>

  );
}

export default Write