import { Grid, Typography, Container, CardContent, Card } from "@mui/material"
import { useEffect, useState } from "react"
import { Film, StateMarks, Mark } from "../../utils/mybase"




const Rate = (prop: { v: Film }) => {

  const [marks, setMarks] = useState([] as Mark[])

  useEffect(() => {
    StateMarks(prop.v,[marks, setMarks])
  }, [prop.v])



    return (
        <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
            <Grid container spacing={3}>
                
            {marks.map((m)=><Comment v={m}/>)}
            </Grid>
        </Container>





    )

}

export default Rate



const Comment = (prop:{v:Mark}) =>{

    
    var v = prop.v
    return (
        <Grid item xs={12} md={12}>
          <Card >
              <CardContent>
                  
                <Typography component="h4" variant="h5">
                  {v.uid}
                </Typography>

                <Typography variant="subtitle1" paragraph>
                  {v.comment}
                </Typography>

                <Typography variant="subtitle1" color="textSecondary">
                  {v.date?.toDate().toISOString().slice(0, 10)}
                </Typography>

              </CardContent>
          </Card>
      </Grid>
    )
}
