import * as React from 'react';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Paper from '@mui/material/Paper';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import Button from '@mui/material/Button';
import Link from '@mui/material/Link';
import Typography from '@mui/material/Typography';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import One from './One';
import AddImg from './Two';
import { Film } from '../../utils/mybase';
import { useState } from 'react';
import Confirm from './Done';


function Copyright() {
  return (
    <Typography variant="body2" color="text.secondary" align="center">
      {'Copyright © '}
      <Link color="inherit" href="https://material-ui.com/">
        Your Website
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const steps = ['Add Movie', 'Add Image', 'Preview'];



const theme = createTheme();



export default function Checkout() {

  const [movie, setMovie] = useState({
      description: '',
      marks: Array(),
      title:'',
      src:"https://source.unsplash.com/random"
    } as Film)

  const [activeStep, setActiveStep] = React.useState(0);


  



  const getStepContent = (step: number) =>{
    switch (step) {
      case 0:
        return <One value={[movie,setMovie]}/>;
      case 1:
        return <AddImg value={[movie,setMovie]}/>;
      default:
        return <p>case 3</p>
    }
  }

  var Commander = (
    <React.Fragment>
      {getStepContent(activeStep)}
      <Box sx={{ display: 'flex', justifyContent: 'flex-end' }}>
        {activeStep !== 0 && (
          <Button onClick={()=>setActiveStep(activeStep - 1)} sx={{ mt: 3, ml: 1 }}>
            Back
          </Button>
        )}
        <Button
          variant="contained"
          onClick={()=>setActiveStep(activeStep + 1)}
          sx={{ mt: 3, ml: 1 }}
        >
          {activeStep === steps.length - 1 ? 'Place order' : 'Next'}
        </Button>
      </Box>
    </React.Fragment>
  )


  




  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="sm" sx={{ mb: 4 }}>
        <Paper variant="outlined" sx={{ my: { xs: 3, md: 6 }, p: { xs: 2, md: 3 } }}>
          <Typography component="h1" variant="h4" align="center">
            Add Movie
          </Typography>
          <Stepper activeStep={activeStep} sx={{ pt: 3, pb: 5 }}>
            {steps.map((label) => (
              <Step key={label}>
                <StepLabel>{label}</StepLabel>
              </Step>
            ))}
          </Stepper>
          <React.Fragment>
            {activeStep === steps.length? <Confirm val={movie}/>:Commander}
          </React.Fragment>
        </Paper>
        <Copyright />
      </Container>
    </ThemeProvider>
  );
}



