import { Typography } from "@mui/material"
import React, { useEffect, useState } from "react"
import { Link } from "react-router-dom"
import { AddMovie, Film, Now } from "../../utils/mybase"


const Confirm = (prop: { val: Film }) => {

    useEffect(() => {
        var a = prop.val
        a.date = Now()
        AddMovie(a).then((f) => setView(<Result id={f.id} />), (e) => setView(<Error e={e}/>))

    }, [prop.val])

    const [View, setView] = useState(() => (<p> Loading</p>))



    const Error = (e:any) => (<p>Error: {e}</p>)

    const Result = (prop: { id: string }) => {

        return (
            <React.Fragment>
                <Typography variant="h5" gutterBottom>
                    Movie have been added.
                </Typography>
                <Typography variant="subtitle1">

                    Your Movie id is {prop.id}.
                    We added your movie,and it will be avalible
                    clicking the following link <br></br>
                    <Link to={"/Movie/?" + prop.id}> Here </Link>
                </Typography>
            </React.Fragment>
        )
    }





    return (
        <>
            {View}
        </>
    )




}

export default Confirm