import { Grid,Checkbox,FormControlLabel,TextField,Typography} from '@mui/material';
import { Box } from '@mui/system';
import { Film } from '../../utils/mybase';
import { State } from '../../utils/types';

export default function AddImg(prop: { value: State<Film> }) {
  const setVal =(call:(f:Film)=>void)=>{
    const c = prop.value[0]
    call(c)
    prop.value[1](c)
  }

fetch('https://source.unsplash.com/random')
    .then((e)=>{setVal((f)=>f.src = e.url)})
  

  return (
    <div>
      <Typography variant="h6" gutterBottom>
        Add Image
      </Typography>
      <Grid container spacing={3}>
        <Grid item xs={12} md={6}>
          <TextField
            onChange={(event)=>{setVal((f)=>f.src = event.target.value)}}
            id="imgUrl"
            label="imgUrl"
            fullWidth
  
          />
        </Grid>

        <Grid item xs={12} md={6}>
          <Box
            component="img"
            sx={{
              maxHeight: { xs: 233, md: 167 },
              maxWidth: { xs: 350, md: 250 },
            }}
            alt="Set a valid image url"
            src={prop.value[0].src}
          />
        </Grid>
        <Grid item xs={12}>
          <FormControlLabel
            control={<Checkbox color="secondary" name="saveCard" value="yes" />}
            label="Checkbox casuale"
          />
        </Grid>
      </Grid>
    </div>
  );
}


