export const fireauth = getAuth(app)
export const db = getFirestore(app)


export const dbMovie = collection(db, "movies")
export const dbMarks = collection(db, "marks")

export const signInWithGoogle = () => {
  const provider = new GoogleAuthProvider();
  signInWithPopup(fireauth, provider)
}

export type Film = {

  title: string,
  description: string,
  src: string,
  date?: any,
  id: string
  marks: DocumentReference<Mark>[]
}



export const Now = serverTimestamp

export interface Mark {
  uid?: string,
  comment: string,
  date?: any,
  vote: number,
  movieId:string


}


export const AddMovie = async (v: Film) => {

  v.date = serverTimestamp()
  v.marks = []


  const a = await addDoc(collection(db, "movies"), v);
  return a

}

export const AddMark = async (v: Mark, movieID: string) => {

  // console.log('maybase movieID'+movieID);
  
  const ref = await addDoc(collection(db, "marks"), v)

  const val = await updateDoc(doc(db, "movies", movieID),
    {
      marks: arrayUnion(ref.id)
    }
  )

  return val
    

}

export const StateMarks = async (v: Film, s: State<Mark[]>) => {

  const q = query(collection(db, "marks"), where("movieId", "==", v.id));

  onSnapshot(q, (querySnapshot) => {
    const res = [] as Mark[];
    querySnapshot.forEach((doc) => {
        res.push(doc.data() as Mark);
    });

    s[1](res)
  });


  return s[0]

}
export const StateMovie = async (id: string, s: State<Film>) => {

  onSnapshot(doc(db, "movies", id), (doc) => {
    const a = doc.data() as Film
    a.id = doc.id
    s[1](a)
  })

}






