import { Film, StateMovie } from '../../utils/mybase';
import { AddComment } from '@material-ui/icons';
import Rate from './Rate';


import { useEffect, useState } from 'react';

import { Box, Container, Grid, IconButton, Link, Paper, Typography } from '@mui/material';
import Write from './Write';

const Movie = (prop: { location: { search: string, hash: string } }) => {

  const [film, setFilm] = useState({} as Film)
  const c = prop.location.search.substring(1)

  
  useEffect(() => {
    StateMovie(c,[film,setFilm])
    console.log("diocane");
    
  },[])




  return (
    <div>

      {film.id ?

        <Page v={film} />
        :
        <p>Error page not Found  </p>}


    </div>
  )
}

export const Page = (prop: { v: Film }) => {

  const [write, setWrite] = useState(false)





  return (
    <>
    <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
      <Grid container spacing={3}>

        <Grid item xs={12}>
          <Pick film={prop.v} />
        </Grid>



        {/* Header */}
        <Grid item xs={2}>
          <Typography variant="h4" marginLeft="30px">
            Comments
          </Typography>
        </Grid>

        <Grid item xs={2}>
          <IconButton onClick={() => { setWrite(true) }}>
            <AddComment />

          </IconButton>
          <Write v={[write, setWrite]} film={prop.v} />
        </Grid>

        



      </Grid>


    </Container>

    {prop.v.marks.length && <Rate v={prop.v}/>} 
    </>
    
  )

}

export default Movie






const Pick = (p: { film: Film }) => {return(
  <Paper
    sx={{
      position: 'relative',
      backgroundColor: 'grey.800',
      color: '#fff',
      mb: 4,
      backgroundSize: 'cover',
      backgroundRepeat: 'no-repeat',
      backgroundPosition: 'center',
      backgroundImage: `url(${p.film.src})`,
    }}
  >
    {/* Increase the priority of the hero background image */}
    {<img style={{ display: 'none' }} src={p.film.src} />}
 
    <Grid container>
      <Grid item md={6}>
        <Box
          sx={{
            position: 'relative',
            p: { xs: 3, md: 6 },
            pr: { md: 0 },
          }}
        >
          <Typography component="h1" variant="h3" color="inherit" gutterBottom>
            {p.film.title}
          </Typography>
          <Typography variant="h5" color="inherit" paragraph>
            {p.film.description}
          </Typography>
          <Link variant="subtitle1" href="#workinProgress">
            Whatch Now!!!
          </Link>
        </Box>
      </Grid>
    </Grid>
  </Paper>
)}
